<template>
    <div id="edit-user">
        <h3>Edit User</h3>
        <div class="row">
            <form @submit.prevent="updateUser" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="user_id" required>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="access_level" required>
                        
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>
<script>
import db from './firebaseInit'
export default {
    name: 'edit-user',
    data () {
        return {
            user_id: null,
            name: null,
            access_level: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('users').where('user_id','==', to.params.user_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.user_id = doc.data().user_id
                    vm.name = doc.data().name
                    vm.access_level = doc.data().access_level
                })
            })
        })
        
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('users').where('user_id', '==', 
            this.$route.params.user_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.user_id = doc.data().user_id 
                        this.name = doc.data().name
                        this.user_access = doc.data().user_access
                })
            })
        },
        updateUser () {
            db.collection('users').where('user_id', '==', 
            this.$route.params.user_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            user_id: this.user_id,
                            name: this.name,
                            access_level: this.access_level

                        })
                        .then(() => {
                            this.$router.push({name: 'view-user', 
                            params: {user_id: this.user_id}})
                        })
                })
            })
        }
        
    }
}
</script>


