<template>
  <div id="dashboard">
    <!-- Collection is a materialize fancy ul -->
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Users</h4>
      </li>
      <li v-for="user in users" v-bind:key="user.id" class="collection-item">
        <div class="chip">{{user.access_level}}</div>
        {{user.user_id}}: {{user.name}}

        <router-link class="secondary-content" v-bind:to="{name: 'view-user', params: {user_id: user.user_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
      </ul>
    
    

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit"
export default {
  name: "dashboard",
  data() {
    return {
      users: []
    }
  },
  created () {
      db.collection('users').orderBy('name').get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          //console.log(doc.id);  //displays user id to console.log
          //console.log(doc.data());  //displays everything else to console.log
          
          
          // the stuff in data links up to the objects within templates (search this file for access_level) (matched by name e.g. access_level)
          const data = {
            'id': doc.id,
            'user_id': doc.data().user_id,
            'name': doc.data().name,
            'access_level': doc.data().access_level
          }
          this.users.push(data)
        })
      })
    }
      
}
</script>


