<template>
    <div id="view-user">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">User ID#: {{user_id}}</li>
            <li class="collection-item">Access level: {{access_level}}</li>
            
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteUser" class="btn red">Delete</button>
         <div class="fixed-action-btn">
      <router-link v-bind:to="{name: 'edit-user', 
      params: {user_id: user_id}}" 
      class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
    </div>
</template>
<script>
import db from './firebaseInit'
export default {
    name: 'view-user',
    data () {
        return {
            user_id: null,
            name: null,
            access_level: null
        }

    },
    beforeRouteEnter(to, from, next) {
        db.collection('users').where('user_id','==', to.params.user_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.user_id = doc.data().user_id
                    vm.name = doc.data().name
                    vm.access_level = doc.data().access_level
                })
            })
        })
        
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('users').where('user', '==', 
            this.$route.params.user_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.user_id = doc.data().user_id 
                        this.name = doc.data().name
                        this.user_access = doc.data().user_access
                })
            })
        },
        deleteUser () {
            if(confirm('Are you sure?')) {
                db.collection('users').where('user_id', '==',
                this.$route.params.user_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>


